<template>
  <div>
    <el-form ref="formTitle" :model="formTitle" :rules="rulesTitle" label-width="80px">
      <el-form-item label="题库名称" prop="title">
        <el-input v-model="formTitle.title" placeholder="请输入题库名称" maxlength="80" show-word-limit></el-input>
      </el-form-item>
      <el-form-item style="text-align:center;">
        <div v-if="dataEdit">
          <el-button size="mini" @click="handleCancel">取消</el-button>
          <el-button :loading="btnLoading" size="mini" type="primary" @click="saveFormTitle('formTitle')">提交修改
          </el-button>
        </div>
        <div v-else>
          <el-button size="mini" @click="$router.go(-1)">返回</el-button>
          <el-button :loading="btnLoading" size="mini" type="primary" @click="saveFormTitle('formTitle')">下一步
          </el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from "@/scripts/api";

export default {
  name: "LibraryTitle",
  props: {
    practiceLibrary: {
      type: String,
      default: null
    }
  },
  data () {
    return {
      formTitle: {
        id: "",
        title: ""
      },
      rulesTitle: {
        title: {
          required: true,
          min: 1,
          max: 80,
          message: "长度在 1 到 80 个字符",
          trigger: "blur"
        }
      },
      btnLoading: false,
      dataEdit: false
    };
  },
  created () {
    if (this.practiceLibrary) {
      this.dataEdit = true;
      this.formTitle.id = this.practiceLibrary.id;
      this.formTitle.title = this.practiceLibrary.title;
    }
  },
  methods: {
    /**
     * 保存标题
     */
    saveFormTitle (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          api.IClassPractise.LibraryDataSave(
            this.formTitle.id,
            "",
            this.formTitle.title
          ).then(res => {
            if (res.Result) {
              if (res.Data) this.formTitle.id = res.Data;
              this.$message.success("操作成功");
              this.$emit("change", this.formTitle);
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          }).catch(() => {
            this.btnLoading = false;
          });
        } else {
          this.$message.error("请输入正确格式");
          return false;
        }
      });
    },
    handleCancel () {
      this.$emit("cancel");
    }
  }
};
</script>

