<template>
  <div class="filter">
    <el-button size="mini" type="primary" plain @click="ChangeExpand">
      {{ expand ? "收起" : "展开" }}筛选
      <i class="el-icon-arrow-up" v-if="expand" />
      <i class="el-icon-arrow-down" v-else />
    </el-button>
    <div v-if="expand">
      <el-card shadow="hover" v-for="(item, index) in FilterItems" :key="index">
        <div>
          <el-checkbox
            v-if="item.Multiple && item.Type !== 'ClsRoom'"
            :indeterminate="item.indeterminate"
            v-model="item.checkedALL"
            @change="checked => ALLCheckChange(checked, item)"
            >{{ item.TypeName }}</el-checkbox
          >
          <span v-else>{{ item.TypeName }}</span>
        </div>
        <div
          :class="{ more: showMore === item.Type }"
          :ref="`divOptions${index}`"
        >
          <div v-if="item.Type === 'Date-TimeRange'">
            <DTRange
              :value.sync="item.Values"
              @change="val => DTRangeChange(val, item)"
              :NotEralier.sync="item.NotEarlier"
            />
          </div>
          <Room
            :value="item.Values"
            v-if="item.Type === 'ClsRoom'"
            :Level="item.Level"
            :multiple="item.Multiple"
            @Filter="val => RoomChange(val, item)"
          />
          <el-checkbox-group
            size="mini"
            v-model="item.Floors"
            v-if="item.Type === 'ClsRoom' && item.Floors"
            @change="checked => FloorsChange(checked, item)"
          >
            <el-checkbox-button
              :label="floor"
              v-for="floor in Floors"
              :key="floor"
              >{{ floor }}层</el-checkbox-button
            >
          </el-checkbox-group>
          <el-checkbox-group
            size="mini"
            v-model="item.Values"
            v-if="item.Multiple"
            @change="checked => ValueChange(checked, item)"
          >
            <el-checkbox-button
              :label="option.ID"
              v-for="(option, opionIndex) in item.Options"
              :key="opionIndex"
              >{{ option.Label }}</el-checkbox-button
            >
          </el-checkbox-group>
          <el-radio-group
            size="mini"
            v-model="item.Values"
            @change="checked => ValueChange(checked, item)"
            v-else
          >
            <el-radio-button
              :label="option.ID"
              v-for="(option, opionIndex) in item.Options"
              :key="opionIndex"
              >{{ option.Label }}</el-radio-button
            >
          </el-radio-group>
        </div>
        <el-checkbox-group
          v-show="item.ShowEx"
          size="mini"
          v-model="item.ExOptions"
          @change="checked => ChangeEx(checked, item)"
        >
          <el-checkbox-button label="more">更多</el-checkbox-button>
        </el-checkbox-group>
      </el-card>
    </div>
  </div>
</template>

<script>
import api from "@/scripts/api";
import moment from "moment";
export default {
  components: {
    Room: () => import("@/components/CascaderTree"),
    DTRange: () => import("@/components/DateTimeRange")
  },
  props: {
    /**
     * 筛选项数组：
     * 格式：
     * Type: 字符串
     *      ClsRoomType：     教室类型
     *      RoomSeats：       教室座位数
     *      Date-TimeRange    日期 + 时段
     *      ClsRoom           教室区域
     * Multiple: 是否为多选项
     * TypeName: 类型名称
     */
    FilterItems: {
      type: Array,
      default: () => []
    },
    Expand: {
      type: Boolean,
      default: true
    }
  },
  // watch: {
  //   FilterItems(newVal, oldVal) {
  //     console.log(newVal, oldVal);
  //   },
  // },
  data() {
    return {
      Floors: [],
      expand: this.Expand,
      showMore: "",
      datepickerOptions_NotEralier: {
        firstDayOfWeek: this.FirstDayOfWeek,
        disabledDate(time) {
          return time.getTime() < moment(Date.now()).add(-1, "days");
        }
      },
      datepickerOptions: {
        firstDayOfWeek: this.FirstDayOfWeek
      }
    };
  },
  created() {
    this.FilterItems.forEach(async element => {
      this.$set(element, "ExOptions", []);
      this.$set(element, "indeterminate", false);
      this.$set(element, "checkedALL", false);
      this.$set(element, "ShowEx", false);

      const options = [];
      let val = [];

      let isHasDefault = false;

      if (element.Values) {
        isHasDefault = true;
        val = element.Values;
      }

      switch (element.Type) {
        case "ClsRoomType": {
          this.$set(element, "TypeName", "教室类型");
          const res = await api.ClsRoom.GetClsRoomTypes();
          if (res) {
            if (!element.Multiple) {
              options.push({
                ID: null,
                Label: "不限"
              });

              if (!isHasDefault) val = null;
            }
            if (res.Datas) {
              res.Datas.forEach(item => {
                options.push({
                  ID: item.MetadataCode,
                  Label: item.MetadataName
                });
              });
            }
          }

          break;
        }
        case "DeviceType": {
          options.push(
            {
              ID: 1,
              Label: "智能终端"
            },
            {
              ID: 2,
              Label: "PC"
            },
            {
              ID: 3,
              Label: "IP电话"
            },
            {
              ID: 4,
              Label: "摄像头"
            },
            {
              ID: 5,
              Label: "课件编码器"
            },
            {
              ID: 7,
              Label: "IC卡读卡器"
            },
            {
              ID: 9,
              Label: "录播主机"
            },
            {
              ID: 11,
              Label: "投影仪"
            },
            {
              ID: [
                8,
                20,
                21,
                22,
                23,
                24,
                30,
                40,
                41,
                42,
                43,
                44,
                45,
                46,
                47,
                48
              ],
              Label: "物联网设备"
            }
          );

          break;
        }
        case "RoomSeats": {
          this.$set(element, "TypeName", "教室座位数");
          options.push({ ID: 0, Label: `不限` });
          for (let i = 30; i <= 140; i += 10) {
            options.push({ ID: i, Label: `${i}席` });
          }
          val = 0;
          break;
        }
        case "Date-TimeRange": {
          val.push(new Date());
          val.push([new Date(), moment(new Date()).add(1, "hours")]);
          break;
        }
      }

      this.$set(element, "Options", options);
      this.$set(element, "Values", val);
    });
  },
  async mounted() {
    this.$nextTick(() => {
      this.$emit("Loaded");
    });

    const res = await api.ClsRoom.GetFloorByCampusBuilding();
    if (res) {
      this.Floors = res.Datas;
    }
  },
  updated() {
    this.$nextTick(() => {
      this.AdjustShowEx();
    });
  },
  methods: {
    // 教室筛选变化时
    RoomChange(val, item) {
      this.$emit("Filter", item.Type, val, item.Floors);
    },
    // 时间范围筛选变化时
    DTRangeChange(val, item) {
      this.$emit("Filter", item.Type);
    },
    // 项目选中变化时
    ValueChange(val, item) {
      // console.log(val, item);
      if (item.Multiple) {
        if (val && val.length > 0) {
          item.checkedALL = true;
          if (val.length === item.Options.length) {
            item.indeterminate = false;
          } else {
            item.indeterminate = true;
          }
        } else {
          item.indeterminate = false;
          item.checkedALL = false;
        }
      }

      this.$emit("Filter", item.Type, val);
    },
    FloorsChange(val, item) {
      this.$emit("Filter", "ClsRoom-Floors", val);
    },
    // ChangeValue(itemIndex, val) {
    //   const item = this.FilterItems[itemIndex];
    //   this.ValueChange(val, item);
    //   if (val) {
    //     item.Values = val.toString();
    //     this.$set(item, "Values", val.toString());
    //     console.log(item, val);
    //   }
    // },
    // 全选
    ALLCheckChange(val, item) {
      if (val) {
        const selecteds = [];
        item.Options.forEach(opt => {
          selecteds.push(opt.ID);
        });
        item.Values = selecteds;
      } else {
        item.Values = [];
      }
      item.indeterminate = false;
      item.checkedALL = val;

      this.$emit("Filter", item.Type, item.Values);
    },
    // 尾部的扩展选择
    ChangeEx(val, item) {
      this.FilterItems.forEach(fi => {
        if (fi.Type !== item.Type) {
          fi.ExOptions = [];
        }
      });
      item.ShowMore = val.indexOf("more") >= 0;
      if (val.indexOf("more") >= 0) {
        this.showMore = item.Type;
      } else {
        this.showMore = "";
      }

      this.$nextTick(() => {
        this.$emit("ReSize");
      });
    },
    // 展开|收缩
    ChangeExpand() {
      this.expand = !this.expand;
      this.$nextTick(() => {
        this.$emit("ReSize");
      });
    },
    // 调整是否显示后面的更多操作
    AdjustShowEx() {
      if (this.expand) {
        this.FilterItems.forEach((fi, index) => {
          const divOptions = this.$refs[`divOptions${index}`][0];
          const divGroups = divOptions.children[0];

          const pHeight = parseInt(window.getComputedStyle(divOptions).height);
          const sHeight = parseInt(window.getComputedStyle(divGroups).height);

          if (sHeight > pHeight) {
            fi.ShowEx = true;
          }
        });
      }
    }
  }
};
</script>

<style lang="less">
.filter {
  position: relative;

  > .el-button {
    position: absolute;
    right: @Margin;
    top: -30px - 5px;
  }

  > div {
    margin: auto @Margin;
    border: 1px solid #ebeef5;

    .el-card {
      border: none;
      border-bottom: 1px dashed #ebeef5;

      .el-card__body {
        @LabelWidth: 120px;
        @ActionWidth: 76px;
        @Height: 38px;

        padding: 0px;
        display: grid;
        align-items: center;
        grid-template-columns: @LabelWidth auto @ActionWidth;

        > div {
          padding: 0 10px;
          position: relative;
          display: flex;
          align-items: center;
        }

        > div:first-child {
          background-color: rgb(245, 246, 247);
          text-align: right;
          height: 100%;
          flex-direction: row-reverse;

          span {
            font-size: 14px;
            font-weight: 700;
            color: #606266;
          }
        }

        > div:nth-child(2) {
          display: flex;
          flex-wrap: wrap;
          height: @Height;
          align-items: center;
          flex-direction: row;
          overflow: hidden;

          .el-date-editor--date,
          .el-date-editor--timerange,
          .el-cascader {
            width: 200px;
          }
        }

        > div.more {
          // height: @Height * 2 !important;
          height: auto !important;
          overflow-x: hidden;
          overflow-y: auto;
        }

        > div:last-child {
          .el-checkbox-button {
            .el-checkbox-button__inner {
              color: #409eff;
              background: #ecf5ff;
              border-color: #b3d8ff;
            }
          }

          .el-checkbox-button.is-checked {
            .el-checkbox-button__inner {
              border-left: 1px solid #409eff;
              background: #409eff;
              color: #fff;
            }
          }
        }
      }
    }

    .el-card:last-child {
      border: none;
    }
  }
}
</style>
