<template>
  <div class="discussion">
    <div class="item-hd">
      <div class="line-title">小组讨论</div>
      <div>
        <el-button type="primary" size="mini" @click="searchData" :disabled="disabled">刷新</el-button>
        <el-button type="primary" size="mini" @click="dataAdd" :disabled="disabled">添加</el-button>
      </div>
    </div>

    <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowExport="false" :ShowSelectColumn="false"
           :Loading="searchLoading" :Columns="dataColumns" :Datas.sync="dataList" :UnNeedEstateBtn="true"
           @LoadData="searchData" :showMaxeight="false">
      <template slot="createTime" slot-scope="scope">
        <span>{{formatDate(scope.row.createTime, "yyyy-MM-dd")}}</span>
      </template>
      <template slot="oprate" slot-scope="scope">
        <el-button-group>
          <el-button plain type="success" size="mini" icon="el-icon-edit" @click="dataEdit(scope.row)"
                     :disabled="disabled">
            修改
          </el-button>
          <el-button plain type="danger" size="mini" icon="el-icon-close" @click="dataDrop(scope.row)"
                     :disabled="disabled">
            移除
          </el-button>
        </el-button-group>
      </template>
    </Table>

    <el-dialog title="小组讨论编辑" :visible.sync="showEdit" width="600px" center :close-on-click-modal="false"
               :modal-append-to-body="false">
      <el-form ref="form" :model="form" :rules="rules" size="small">
        <el-form-item label="" prop="title">
          <el-input type="textarea" v-model="form.title" :autosize="{ minRows: 3, maxRows: 10}" placeholder="请输入小组讨论题目"
                    maxlength="500" :show-word-limit="true">
          </el-input>
        </el-form-item>
        <el-form-item style="text-align:center;">
          <el-button :loading="btnLoading" type="primary" @click="dataSave('form')">保 存</el-button>
          <el-button @click="showEdit = false">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";

export default {
  components: {
    Table: () => import("@/components/Table")
  },
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      classHourID: getSession("campus_ClassHourID"),
      showEdit: false,
      searchLoading: false,
      dataColumns: [
        {
          Code: "content",
          Header: "题目",
          Fixed: "left"
        },
        {
          Code: "createTime",
          Slot: true,
          Header: "创建时间"
        },
        {
          Code: "oprate",
          Header: "操作",
          Slot: true,
          width: 200
        }
      ],
      dataList: [],
      form: {
        id: "",
        title: "",
        time: 0
      },
      rules: {
        title: {
          required: true,
          min: 1,
          max: 500,
          message: "长度在 1 到 500 个字符",
          trigger: "blur"
        },
        time: [
          { required: true, message: "试卷计时不能为空" },
          { type: "number", message: "试卷计时必须为数字值" }
        ]
      },
      btnLoading: false
    };
  },
  created () {
    this.searchData();
  },
  methods: {
    searchData () {
      this.searchLoading = true;
      this.dataList = [];
      api.IPrepareGroupSubject.DataList(this.classHourID).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              id: item["PGSID"],
              time: item["PGSTime"],
              content: item["PGSContent"],
              createTime: item["CreateTime"],
              state: item["State"],
              classHourID: item["CHID"]
            });
          });
          this.$refs["table"].MaxTblHeight = 500;
        }
        this.searchLoading = false;
      }).catch(() => {
        this.searchLoading = false;
      });
    },
    dataEdit (row) {
      this.form.id = row.id;
      this.form.title = row.content;
      this.showEdit = true;
    },
    dataSave (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          api.IPrepareGroupSubject.DataSave(this.form.id, this.classHourID, this.form.title, this.form.time).then(res => {
            if (res.Result) {
              this.$message.success("保存成功！");
              setTimeout(() => {
                this.searchData();
              }, 100);
              this.showEdit = false;
            } else if (res.Info) {
              this.$message.error(res.Info);
            }
            this.btnLoading = false;
          }).catch(() => {
            this.btnLoading = false;
          });
        } else {
          this.$message.warning("请输入正确格式");
          return false;
        }
      });
    },
    dataDrop (row) {
      this.$confirm("此操作将删除小组讨论, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.dataDelete(row);
      }).catch(() => {
      });
    },
    dataDelete (row) {
      api.IPrepareGroupSubject.DataDelete(row.id).then(res => {
        if (!res.Result) {
          if (res.Info) this.$message.error(res.Info);
        } else {
          this.$message.success("删除成功");
          this.searchData();
        }
      }).catch(() => {
        this.$message.error("操作失败");
      });
    },
    dataAdd () {
      this.formReset();
      this.showEdit = true;
    },
    formReset () {
      this.form = {
        id: "",
        title: "",
        time: 0
      };
      if (this.$refs["form"]) this.$refs["form"].resetFields();
    },
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    }
  }
};
</script>
<style lang="less" scoped>
</style>
