<template>
  <div class="library-subject">
    <div class="edit_title">编辑第{{ (subjectIndex + 1) }}题</div>
    <el-form ref="formSubject" :model="formSubject" size="small" :rules="rulesSubject" label-width="80px">
      <el-form-item label="题目类型" prop="type">
        <el-select v-model="formSubject.type" placeholder="请选择题目类型" style="width:100%;" @change="subjectTypeChange">
          <el-option v-for="(item, index) in subjectTypeOptions" :key="index" :label="item.label"
                     :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="题目标题" prop="title">
        <el-input type="textarea" v-model="formSubject.title" :autosize="{ minRows: 1, maxRows: 6}"
                  placeholder="请输入题目标题" maxlength="1000" show-word-limit>
        </el-input>
      </el-form-item>

      <el-form-item v-if="[1, 2].indexOf(formSubject.type) > -1" label="题目选项" required>
        <div style="text-align: right; margin-right: 15px;">
          <el-button v-show="formSubject.options.length<8" type="primary" size="mini" @click="subjectOptionsAdd">
            新增
          </el-button>
        </div>
        <el-table size="mini" :data="formSubject.options" :cell-style="{border:0}"
                  style="width: 100%; margin-bottom:0;">
          <el-table-column prop="value" label="选项" width="60"></el-table-column>
          <el-table-column prop="label" label="选项内容">
            <template slot-scope="scope">
              <el-form-item :prop="'options.' + scope.$index + '.label'" :rules="rulesSubject.options.label"
                            style="margin-bottom: 15px;">
                <el-input v-model="scope.row.label" placeholder="请输入选项内容"></el-input>
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-button v-show="formSubject.options.length>2" type="danger" size="mini"
                         @click="subjectOptionsDelete(scope.row)">删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>

      <el-form-item v-if="formSubject.type===2" label="题目答案" prop="multiAnswer">
        <el-checkbox-group v-model="formSubject.multiAnswer">
          <el-checkbox-button v-for="(item, index) in formSubject.options" :key="index" :label="item.value"
                              :value="item.value">
          </el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item v-else label="题目答案" prop="answer"
                    :rules="{required: formSubject.type!==4, message: '请选择或输入题目答案', trigger: 'change'}">
        <el-radio-group v-if="formSubject.type===1" v-model="formSubject.answer">
          <el-radio-button v-for="(item, index) in formSubject.options" :key="index" :label="item.value"
                           :value="item.value">
          </el-radio-button>
        </el-radio-group>

        <el-radio-group v-else-if="formSubject.type===3" v-model="formSubject.answer">
          <el-radio-button v-for="(item, index) in judgeOptions" :key="index" :label="item" :value="item">
          </el-radio-button>
        </el-radio-group>

        <el-input v-else type="textarea" v-model="formSubject.answer" :autosize="{ minRows: 2, maxRows: 6}"
                  placeholder="请输入题目答案" maxlength="1000" show-word-limit>
        </el-input>
      </el-form-item>

      <el-form-item label="题目分数" prop="score">
        <el-input-number v-model="formSubject.score" :min="1" :max="1000" label="请输入题目分数" controls-position="right"
                         :precision="0" :step="1">
        </el-input-number>
      </el-form-item>

      <el-form-item label="题目解析" prop="analysis">
        <el-input type="textarea" v-model="formSubject.analysis" :autosize="{ minRows: 2, maxRows: 6}"
                  placeholder="题目解析" maxlength="1000" show-word-limit>
        </el-input>
      </el-form-item>

      <el-form-item style="text-align:center;">
        <div v-if="updateMode">
          <el-button size="mini" @click="handleCancel">取消</el-button>
          <el-button size="mini" :loading="btnLoading" type="primary" @click="saveFormSubject">
            <span v-if="formSubject.isAdd">提交保存</span>
            <span v-else>提交修改</span>
          </el-button>
        </div>
        <div v-else>
          <el-button size="mini" @click="handleCancel">关闭</el-button>
          <el-button size="mini" :loading="btnLoading" :disabled="btnDisabled" type="primary" @click="saveFormSubject">
            保存
          </el-button>
          <el-button size="mini" v-if="btnDisabled" type="primary" @click="nextSubject()">下一题</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from "@/scripts/api";

export default {
  name: "LibrarySubject",
  props: {
    fromId: {
      type: String,
      default: ""
    },
    index: {
      type: Number,
      default: 0
    },
    updateMode: {
      type: Boolean,
      default: false
    },
    subjectSelected: {
      type: Object
    }
  },
  data () {
    return {
      subjectIndex: this.index || 0,
      subjectTypeOptions: [
        {
          label: "单选题",
          value: 1
        },
        {
          label: "多选题",
          value: 2
        },
        {
          label: "判断题",
          value: 3
        },
        {
          label: "问答题",
          value: 4
        }
      ],
      singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
      judgeOptions: ["对", "错"],

      formSubject: {
        isAdd: true,
        fromId: "", // this.fromId,
        type: 1,
        title: "",
        options: [],
        answer: "",
        multiAnswer: [],
        score: 0,
        analysis: ""
      },
      rulesSubject: {
        title: {
          required: true,
          min: 1,
          max: 500,
          message: "长度在 1 到 500 个字符",
          trigger: "blur"
        },
        options: {
          label: {
            required: true,
            min: 1,
            max: 50,
            message: "长度在 1 到 50 个字符",
            trigger: "blur"
          },
          value: {
            required: true,
            min: 1,
            max: 50,
            message: "长度在 1 到 50 个字符",
            trigger: "blur"
          }
        },
        // answer: { required: true, message: "请选择或输入题目答案", trigger: "change" },
        multiAnswer: {
          required: true,
          type: "array",
          message: "请至少选择一个答案",
          trigger: "change"
        },
        score: [
          {
            required: true,
            message: "题目分数不能为空"
          },
          {
            type: "number",
            message: "题目分数必须为数字值"
          }
        ],
        analysis: {
          required: false,
          min: 1,
          max: 500,
          message: "长度在 1 到 500 个字符",
          trigger: "blur"
        },
        time: [
          {
            required: true,
            message: "试卷计时不能为空"
          },
          {
            type: "number",
            message: "试卷计时必须为数字值"
          }
        ]
      },

      btnLoading: false,
      btnDisabled: false,
      isChanged: false
    };
  },
  mounted () {
    this.$nextTick(() => {
      if (this.$refs["formSubject"]) this.$refs["formSubject"].resetFields();
      this.subjectTypeChange(1);
      if (this.subjectSelected) {
        this.formSubject = this.subjectSelected;
      }
    });
  },
  methods: {
    // 题型选择
    subjectTypeChange (val) {
      if (val === 1 || val === 2) {
        this.formSubject.options = [];
        for (let i = 0; i < 4; i++) {
          this.formSubject.options.push({
            label: "",
            value: this.singleOptions[i]
          });
        }
      }

      this.formSubject.answer = "";
      this.formSubject.multiAnswer = [];
    },
    // 新增选项
    subjectOptionsAdd () {
      if (this.formSubject.type === 1 || this.formSubject.type === 2) {
        this.formSubject.options.push({
          label: "",
          value: this.singleOptions[this.formSubject.options.length]
        });
      }
    },
    // 删除选项
    subjectOptionsDelete (row) {
      if (this.formSubject.options.length < 3) {
        this.$message({
          showClose: true,
          message: "必须有两个以上选项",
          type: "error"
        });
        return;
      }

      const index = this.formSubject.options.indexOf(row);
      if (index > -1) this.formSubject.options.splice(index, 1);
      this.formSubject.options.map((item, i) => {
        item.value = this.singleOptions[i];
      });
    },
    /**
     * 保存题目(单题编辑)
     */
    saveFormSubject () {
      this.$refs["formSubject"].validate(valid => {
        if (valid) {
          this.btnLoading = true;
          const subjectMain = {
            PLID: this.fromId || this.formSubject.fromId, // 以fromId为主
            PSType: this.formSubject.type,
            PSContent: this.formSubject.title,
            PSAnswer: this.formSubject.type === 2 ? this.formSubject.multiAnswer.sort().join("") : this.formSubject.answer,
            PSanalysis: this.formSubject.analysis,
            PSScore: this.formSubject.score
          };
          if (this.updateMode) subjectMain.PSID = this.formSubject.id;
          const subjectOption = {
            Datas: this.formSubject.type === 4 ? [] : this.formSubject.options.map(r => {
              return {
                SOSerial: r.value,
                SOContent: r.label
              };
            })
          };
          // const reqData = {
          //   psdata: JSON.stringify(subjectMain),
          //   sodata: JSON.stringify(subjectOption)
          // };

          if (this.updateMode && !this.formSubject.isAdd) {
            this.dataUpdateSave(JSON.stringify(subjectMain), JSON.stringify(subjectOption))
          } else {
            this.dataAddSave(JSON.stringify(subjectMain), JSON.stringify(subjectOption))
          }
        } else {
          this.$message({
            showClose: true,
            message: "请输入正确格式",
            type: "error"
          });
          return false;
        }
      });
    },
    dataAddSave (main, options) {
      api.IClassPractise.SubjectDataAdd(main, options).then(res => {
        if (res.Result) {
          this.$message({
            message: "操作成功！",
            type: "success"
          });
          if (this.updateMode) {
            this.$emit("change");
          } else {
            this.isChanged = true;
            this.btnDisabled = true;
          }
        } else if (res.Info) {
          this.$message.error(res.Info);
        }
        this.btnLoading = false;
      }).catch(() => {
        this.btnLoading = false;
      });
    },
    dataUpdateSave (main, options) {
      api.IClassPractise.SubjectDataSave(main, options).then(res => {
        if (res.Result) {
          this.$message({
            message: "操作成功！",
            type: "success"
          });
          if (this.updateMode) {
            this.$emit("change");
          } else {
            this.isChanged = true;
            this.btnDisabled = true;
          }
        } else if (res.Info) {
          this.$message.error(res.Info);
        }
        this.btnLoading = false;
      }).catch(() => {
        this.btnLoading = false;
      });
    },
    nextSubject () {
      this.subjectIndex++;
      this.resetFormSubject();
    },
    resetFormSubject () {
      this.btnDisabled = false;

      if (this.$refs["formSubject"]) this.$refs["formSubject"].resetFields();
      this.formSubject.isAdd = true;
      this.formSubject.id = "";
      this.formSubject.type = 1;

      this.formSubject.title = "";
      this.formSubject.answer = "";
      this.formSubject.score = 0;
      this.formSubject.analysis = "";

      this.subjectTypeChange(1);
    },
    handleCancel () {
      this.$emit("cancel", this.isChanged);
    }
  }
};
</script>

<style lang="less">
.library-subject {
  .edit_title {
    text-align: center;
    line-height: 2.5;
    font-weight: bold;
    font-size: 14px;
  }

  .el-textarea .el-input__count {
    right: 5px;
    bottom: -5px;
    background: none;
  }
}
</style>
