<template>
  <el-tooltip content="输入班级关键字可以进行模糊筛选" :placement="TooltipPlacement">
    <el-select
      popper-class="selClass"
      filterable
      :allow-create="ArrowCreate"
      :multiple="multiple"
      v-model="Value"
      :placeholder="placeholder"
      collapse-tags
      @change="ChangeHandle"
      remote
      :size="size"
      clearable
      :remote-method="remoteMethod"
      reserve-keyword
      :loading="loading"
    >
      <el-option
        v-for="item in Datas"
        :key="item.ClassID"
        :label="item.ClassName"
        :value="item.ClassID"
      >
        <span>{{ item.ClassName }}</span>
        <span>{{ item.StuCount }}人</span>
        <span v-if="item.CSID !=='' && item.CSID !== ExCSID">已排课</span>
      </el-option>
    </el-select>
  </el-tooltip>
</template>

<script>
import api from "@/scripts/api";
import moment from "moment";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 允许创建
     */
    ArrowCreate: {
      type: Boolean,
      default: false
    },
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    /**
     * 排课日期
     */
    ScheduleDate: {},
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    /**
     * 排除课表
     */
    ExCSID: {
      type: String,
      default: ""
    },
    /**
     * 排课节次
     */
    ScheduleLesson: {},
    value: {},
    placeholder: {
      type: String,
      default: "请输入关键词"
    }
  },
  watch: {
    value(newVal, oldVal) {
      this.Value = newVal;
      if (newVal !== oldVal) {
        if (this.multiple) {
          if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
            this.ReVerify();
          }
        } else {
          this.LoadData();
        }
      }
    },
    ScheduleDate(newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    },
    ScheduleLesson(newVal, oldVal) {
      if (newVal !== oldVal) this.ReVerify();
    }
  },
  data() {
    return { loading: false, Value: this.value, Datas: [] };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", this.Value);

      if (this.Value && this.multiple) {
        let val = "";
        this.Value.forEach((f, i) => {
          val += f;
          if (i < this.Value.length - 1) {
            val += ",";
          }
        });

        this.$emit("Filter", val);
      }

      let text = "";

      if (this.multiple) {
        const texts = [];

        this.Value.forEach(element => {
          const findC = this.Datas.find(c => c.ClassID === element);
          if (findC) {
            texts.push(`${findC.ClassName}`);
          } else {
            texts.push(element);
          }
        });

        text = JSON.stringify(texts);
        const reg = new RegExp('"', "g"); // g代表全部
        text = text.slice(1, text.length - 1).replace(reg, "");
      } else {
        const findC = this.Datas.find(c => c.ClassID === val);
        if (findC) {
          text = `${findC.ClassName}`;
        } else {
          text = this.Value;
        }
      }

      this.$emit("update:text", text);
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        this.LoadData(query);
      } else {
        this.Datas = [];
      }
    },
    /**
     * 重新验证已选中的值
     */
    async ReVerify() {
      this.loading = true;
      if (this.Value && this.Value.length > 0) {
        this.Value = [];
        const res = await api.Class.GetDataByName(
          "",
          moment(this.ScheduleDate).format("YYYY-MM-DD"),
          this.ScheduleLesson,
          JSON.stringify(this.value)
        );
        if (res) {
          res.Datas.forEach(element => {
            this.$set(
              element,
              "disabled",
              !(element.CSID === "" || element.CSID === this.ExCSID)
            );
            if (element.CSID === "" || element.CSID === this.ExCSID) {
              this.Value.push(element.ClassID);
            }
          });

          this.Datas = res.Datas;

          if (this.ArrowCreate) {
            this.value.forEach(val => {
              if (this.Value.indexOf(val) < 0) {
                this.Value.push(val);
              }
            });
          }

          this.$emit("update:value", this.Value);
        }
        this.loading = false;
      }
    },
    async LoadData(name) {
      const res = await api.Class.GetDataByName(
        name,
        moment(this.ScheduleDate).format("YYYY-MM-DD"),
        this.ScheduleLesson,
        this.multiple ? JSON.stringify(this.value) : `["${this.value}"]`
      );
      if (res) {
        res.Datas.forEach(element => {
          this.$set(
            element,
            "disabled",
            !(element.CSID === "" || element.CSID === this.ExCSID)
          );
        });
        this.Datas = res.Datas;
      }
      this.loading = false;
    }
  }
};
</script>

