<template>
  <div class="log">
    <WeekCalendar Concise :value.sync="ChooseDate" @ValueChanged="DateChanged" />
    <p class="title">
      {{FormatDate(ChooseDate)}}终端日志
      <i
        v-if="FormatDate(ChooseDate) === FormatDate(new Date())"
        class="el-icon-refresh"
        @click="SyncLog"
      />
    </p>
    <div class="lists" v-if="logs">
      <ul>
        <li v-for="(item,index) in logs" :key="index">
          <div>
            <span>{{item.LogTime}}</span>
          </div>
          <div>
            <p>
              <span>动作：{{item.LogAction}}</span>
            </p>
            <p>
              <span>结果：{{item.LogResult}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!logs" class="none">
      <p>{{FormatDate(ChooseDate)}} 无日志</p>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import api from "@/scripts/api";
export default {
  components: {
    WeekCalendar: () => import("@/components/WeekCalendar")
  },
  props: { TerminalInfo: { type: Object, default: () => null } },
  data() {
    return {
      ChooseDate: "",
      logs: null
    };
  },
  mounted() {},
  methods: {
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    },
    FormatSDate(day) {
      return moment(day).format("YYYY-MM-DD") + " 00:00:00";
    },
    FormatEDate(day) {
      return moment(day).format("YYYY-MM-DD") + " 23:59:59";
    },
    DateChanged(date) {
      this.getlog(date + " 00:00:00", date + " 23:59:59");
    },
    // 从终端同步日志
    async SyncLog() {
      const roomid = this.$parent.$route.params.id;
      const res = await api.Ctrl.SyncCtrlLog(
        roomid,
        this.TerminalInfo.DeviceSerialNo,
        this.TerminalInfo.DeviceInfo.IP,
        this.TerminalInfo.DeviceInfo.Port
      );

      if (res.Datas && res.Datas.length > 0) {
        this.LoadLog(res);
      } else {
        this.logs = null;
      }
    },
    async getlog(sDate, eDate) {
      const roomid = this.$parent.$route.params.id;
      this.logs = [];
      const res = await api.Ctrl.GetControlLogBySNoDate(roomid, sDate, eDate);
      if (res.Datas && res.Datas.length > 0) {
        this.LoadLog(res);
      } else {
        this.logs = null;
      }
    },
    LoadLog(res) {
      res.Datas.forEach(element => {
        if (element.LogType === -1) {
          this.logs.push({
            LogTime: moment(element.LogTime).format("HH:mm:ss"),
            LogAction: "设备离线",
            LogResult: "#"
          });
        } else if (element.LogType === 0) {
          this.logs.push({
            LogTime: moment(element.LogTime).format("HH:mm:ss"),
            LogAction: "关机",
            LogResult: "#"
          });
        } else if (element.LogType === 1) {
          this.logs.push({
            LogTime: moment(element.LogTime).format("HH:mm:ss"),
            LogAction: "关机中",
            LogResult: "#"
          });
        } else if (element.LogType === 2) {
          this.logs.push({
            LogTime: moment(element.LogTime).format("HH:mm:ss"),
            LogAction: "开机中",
            LogResult: "#"
          });
        } else if (element.LogType === 3) {
          this.logs.push({
            LogTime: moment(element.LogTime).format("HH:mm:ss"),
            LogAction: "已开机",
            LogResult: "#"
          });
        } else {
          if (element.LogAction) {
            this.logs.push({
              LogTime: moment(element.LogTime).format("HH:mm:ss"),
              LogAction: element.LogAction,
              LogResult: element.LogResult
            });
          }
        }
      });
    }
  }
};
</script>

<style lang="less">
.log {
  .el-icon-refresh {
    cursor: pointer;
  }
  .weekCal {
    margin-top: 0px;

    .weeks {
      .el-card {
        background-color: @ThemeColor;
        color: #ffffff;
        border-radius: 0px;

        .el-card__body {
          .el-badge {
            .week {
              color: fade(#ffffff, 70%);
            }
          }
        }
      }
      .el-card.is-hover-shadow:hover {
        .box-shadow-inset(0, 0, 10px, rgba(255, 255, 255, 0.25));
      }
      .el-card.is-always-shadow {
        .box-shadow-inset(0, 0, 10px, rgba(255, 255, 255, 0.5));
        .el-card__body {
          .el-badge {
            .week {
              color: #ffffff;
            }
          }
        }
      }
    }
  }
  .lists {
    height: calc(100% - 86px - 17px - 20px);

    li {
      > div {
        grid-template-rows: repeat(2);
        align-items: center;
      }
    }
  }
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
}
</style>