<template>
  <div class="hv-tree">
    <div class="box">
      <div
        class="first-node"
        :class="Edit ? 'node-edit' : 'node-view'"
        v-for="(item, i) in scoreTrees"
        :key="i"
      >
        <div class="first-node-head" @click="toggleSpread(item)">
          <span class="title-name">{{ item.GaugeName }}（分值{{ Number(item.GIScore) }}分）</span>
          <div>
            <span v-if="!Edit && only" class="sub-total">总得分：{{Number(item.GSScore)}}分</span>
            <i
              class="el-icon-arrow-up"
              :class="{
              'el-icon-arrow-down': item.isFold,
              'el-icon-arrow-up': !item.isFold
            }"
            ></i>
          </div>
        </div>
        <el-collapse-transition>
          <div class="hv-container" v-show="!item.isFold">
            <div class="second-node" v-for="(sub, j) in item.children" :key="j">
              <div class="content">
                {{ sub.GIContent
                }}
              </div>
              <div class="score">
                <template v-if="Edit">
                  <span>分值：{{ Number(sub.GIScore) }}</span>
                  <el-input-number v-model="sub.GSScore" :min="0" :max="sub.GIScore" size="mini"></el-input-number>
                </template>
                <template v-else>
                  <span>分值：{{ Number(sub.GIScore) }}分</span>
                  <div class="gs-score">得分：{{ Number(sub.GSScore) }}分</div>
                </template>
              </div>
            </div>
            <div class="item-desc-view" v-if="!Edit && item.GSDesc">
              <span>评语：</span>
              <span class="desc">{{item.GSDesc}}</span>
            </div>
            <div class="item-desc" v-if="Edit">
              <div class="desc-title" v-if="!item.needDesc" @click="ShowAdd(scoreTrees, i)">添加评论</div>
              <el-input
                v-if="item.needDesc"
                type="textarea"
                class="scale-area"
                rows="4"
                placeholder="有什么要点评的吗？"
                v-model="item.GSDesc"
              ></el-input>
            </div>
          </div>
        </el-collapse-transition>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    trees: {
      type: Array,
      default: () => []
    },
    Edit: {
      type: Boolean,
      default: false
    },
    only: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    scoreTrees: {
      get() {
        return this.trees;
      },
      set(val) {
        this.$emit("update:trees", val);
      }
    }
  },
  methods: {
    // 点击查看明显
    toggleSpread(item) {
      this.$set(item, "isFold", !item.isFold);
    },
    ShowAdd(item, i) {
      const s = item[i];
      s.needDesc = true;
      item.splice(i, 1, s);
    }
  }
};
</script>
<style lang="less">
.hv-tree {
  //display: none;
  width: 100%;
  color: #999;
  font-size: 12px;

  .box {
    width: 100%;
    margin-top: 8px;

    .first-node {
      width: 100%;
      margin-top: 15px;
      border: 1px solid #ddd;
      &:first-child {
        margin-top: 0;
      }
      .first-node-head {
        box-sizing: border-box;
        width: 100%;
        height: 40px;
        line-height: 40px;
        background: #f5f6fa;
        padding: 0 @Margin;
        user-select: none;
        border-bottom: 1px solid #ddd;
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        .title-name {
          cursor: pointer;
        }
        .sub-total {
          color: #333;
          margin-right: 10px;
        }
        i {
          font-size: 1.52rem;
          line-height: 40px;
          cursor: pointer;
        }
      }

      .hv-container {
        width: 100%;
        padding: 0 @Margin;
        box-sizing: border-box;
        .second-node:last-child {
          border-bottom: none;
        }
        .second-node {
          padding: 10px 0 5px;
          border-bottom: 1px solid #ddd;
          .content {
            line-height: 18px;
          }
          .score {
            display: flex;
            justify-content: space-between;
            line-height: 28px;
            margin-top: 10px;
          }
          .gs-score {
            // float: right;
            // margin-top: -15px;
            color: #333;
          }
        }
        .item-desc-view {
          display: flex;
          padding: @Margin 0;
          > span:first-child {
            width: 38px;
          }
          .desc {
            flex: 1;
            line-height: 18px;
          }
        }
        .item-desc {
          line-height: 18px;
          padding: 12px 0;
          .desc-title {
            text-align: center;
            color: @ThemeColor;
          }
          .el-textarea__inner {
            font-size: 12px;
            line-height: 18px;
          }
        }
      }
    }
  }
}
</style>
