<template>
  <div class="scene">
    <div class="sceneList">
      <div
        class="stritem"
        :class="{'selection' : selIndex === index }"
        v-for="(item,index) in  scenes"
        :key="index"
        @click="selection(item,index)"
      >
        <i class="iconfont" :class="icons[item.icon]"></i>
        <span>{{item.name}}</span>
      </div>
      <div class="increase" @click="increase()">
        <i class="iconfont icon_plus"></i>
        <span>新增</span>
      </div>
    </div>
    <p>情景修改</p>
    <div class="sceneForm">
      <el-form ref="form" :model="form" :rules="rules" size="mini" label-width="90px">
        <el-row :gutter="5">
          <el-col :span="12">
            <el-form-item label="情景名称:" prop="name">
              <el-input v-model="form.name" minlength="2" maxlength="20" clearable show-word-limit></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-popover placement="top" width="210" trigger="hover">
              <span class="custom-iconlist" v-for="(item,index) in icons" :key="index" @click="selectIcon(index)">
                <i class="iconfont" :class="item"></i>
              </span>
              <div slot="reference" class="scene-custom-icon">
                <i class="iconfont" :class="icons[sceneIcon]"></i>
              </div>
            </el-popover>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="scene_actionSet">
      <ActionSet :clsRoomID="roomID" ref="ActionSet" />
    </div>
    <p>
      <el-button-group>
        <el-button
          class="add"
          type="primary"
          icon="el-icon-circle-plus-outline"
          size="mini"
          v-if="isAdd"
          @click="save"
        >新增</el-button>
        <el-button class="update" type="primary" icon="el-icon-edit" size="mini" @click="save" v-if="!isAdd">更新</el-button>
        <el-button class="delete" type="danger" icon="el-icon-close" size="mini" @click="handleDelete" v-if="!isAdd">删除</el-button>
      </el-button-group>
    </p>
  </div>
</template>

<script>
import api from "@/scripts/api";
export default {
  components: {
    ActionSet: () => import("@/views/ctrl/components/ActionSet")
  },
  props: {
    // 教室ID
    clsRoomID: {
      type: String,
      default: null
    }
  },
  watch: {
    clsRoomID (newVal, oldVal) {
      if (newVal) {
        setTimeout(() => {
          this.roomID = newVal;
          this.init();
        }, 200);
      }
    }
  },
  data () {
    return {
      scenes: [],
      isAdd: true,
      selIndex: -1,
      form: { name: "" },
      rules: {
        name: [
          {
            required: true,
            message: "请选择输入名称",
            trigger: "blur"
          }
        ]
      },
      sceneIcon: 0,
      fixID: "", // 修改ID
      fixType: "", // 情景类型
      roomID: "", // 教室ID
      icons: [
        "icon_AttendCls",
        "icon_xiakedianji",
        "icon_Projector",
        "icon_selfstudy",
        "icon_xiuxishi",
        "icon_code",
        "icon_hot",
        "icon_taolun",
        "icon_heiban",
        "icon_split_Custom"
      ]
    };
  },
  methods: {
    // 加载数据
    async init () {
      this.selIndex = -1;
      this.isAdd = true;
      this.scenes = [];
      const res = await api.IOT.GetRHSceneByCondition(this.roomID);
      let s;
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(element => {
          if (element.ClsRoomID === this.roomID) {
            if (element.Scenes && element.Scenes.length > 0) {
              s = JSON.parse(element.Scenes);
              s.forEach(element => {
                this.scenes.push({
                  state: 0,
                  type: element.SceneType,
                  icon: element.SceneIcon - 1,
                  name: element.SceneName,
                  id: element.SceneID
                });
              });
            }
          }
        });
      }
    },
    // 选中
    async selection (obj, index) {
      this.selIndex = index;
      this.isAdd = false;
      const res = await api.IOT.GetSceneByType(obj.id);
      if (res.Result) {
        this.fixID = obj.id;
        this.fixType = obj.type;
        this.form.name = obj.name;
        this.sceneIcon = obj.icon;
        if (res.Datas && res.Datas.length > 0) {
          const actions = [];
          let ele;
          res.Datas.forEach(element => {
            if (element.SceneID === obj.id) {
              ele = JSON.parse(element.ActionContent);
              actions.push({
                type: parseInt(ele.type),
                devName: ele.devName,
                serialID: ele.serialID,
                action: parseInt(ele.action),
                eq: parseInt(ele.eq),
                eqName: ele.eqName,
                w: parseInt(ele.w),
                y: parseInt(ele.y),
                vpid: ele.vpid,
                b0: parseInt(ele.b0),
                b6: parseInt(ele.b6),
                panelName: ele.panelName,
                panelType: parseInt(ele.panelType),
                actionName: ele.actionName,
                index: parseInt(ele.index),
                code: ele.code,
                time: parseInt(ele.time)
              });
              this.$refs.ActionSet.actions = actions;
            }
          });
        }
      }
    },
    // 新增
    increase () {
      this.selIndex = -1;
      this.isAdd = true;
      this.emptyForm();
    },
    // 选中图标
    selectIcon (val) {
      this.sceneIcon = val;
    },
    // 清空表单
    emptyForm () {
      this.fixID = "";
      this.fixType = "";
      this.form.name = "";
      this.sceneIcon = 0;
      this.$refs.ActionSet.actions = [];
    },
    // 保存
    async save () {
      this.$refs.form.validate(async valid => {
        if (valid) {
          const actions = this.$refs.ActionSet.actions;
          if (actions.length > 0 && this.sceneName !== "") {
            actions.forEach((ele, index) => {
              ele.index = index;
            });

            if (this.isAdd) {
              let scenetype = 0;
              this.scenes.forEach(ele => {
                if (ele.type > scenetype) {
                  scenetype = ele.type;
                }
              });
              this.fixType = ++scenetype;
            }

            const res = await api.IOT.SaveScene(
              this.roomID,
              this.form.name,
              this.fixType,
              this.sceneIcon + 1,
              JSON.stringify(actions)
            );
            if (res.Result) {
              this.init();
              this.emptyForm();
              this.$emit("getScene", this.roomID);
            }
          } else {
            this.$message({
              message: "请添加动作集",
              type: "warning"
            });
          }
        }
      });
    },
    // 删除
    async handleDelete () {
      const res = await api.IOT.DeleteScene(this.fixID, this.roomID, parseInt(this.fixType));
      if (res.type) {
        this.$message({
          message: res.message,
          type: res.type
        });
      }
      this.init();
      this.emptyForm();
      this.$emit("getScene", this.roomID);
    }
  }
};
</script>

<style lang="less">
.scene {
  height: 100%;
  width: 100%;
  .sceneList {
    width: 90%;
    height: 20%;
    padding: 0px 20px;
    display: grid;
    grid-gap: 8px;
    grid-template-columns: repeat(4, auto);
    -webkit-box-pack: justify;
    justify-content: space-between;
    overflow: hidden;
    overflow-y: auto;
    > div {
      cursor: pointer;
      height: 80px;
      width: 110px;
      border: 1px solid #e8e8e8;
      display: grid;
      grid-template-columns: 100%;
      justify-items: center;
      -ms-flex-line-pack: center;
      align-content: center;
      background-color: #c7cdd3;
      color: #fff;
      > i {
        cursor: pointer;
        font-style: normal;
        font-size: 30px;
      }
      > span {
        cursor: pointer;
      }
    }
    .selection {
      background: #3a8ee6;
      border-color: #3a8ee6;
      color: #fff;
    }
    .stritem:hover {
      background: #3a8ee6;
      border-color: #3a8ee6;
      color: #fff;
    }
    .increase {
      color: #c7cdd3;
      background-color: #fff;
    }
    .increase:hover {
      color: #409eff;
      background: #ecf5ff;
      border-color: #b3d8ff;
    }
    .increase:active {
      background: #3a8ee6;
      border-color: #3a8ee6;
      color: #fff;
    }
  }
  > p {
    color: #72767b;
    margin: 20px 0px;
    .el-button-group {
      float: right;
      margin-right: 12px;

      .add {
        color: #409eff;
        background: #ecf5ff;
        border-color: #b3d8ff;
      }
      .add:hover {
        color: #fff;
        background-color: #409eff;
        border-color: #409eff;
      }
      .add:active {
        background: #3a8ee6;
        border-color: #3a8ee6;
        color: #fff;
        outline: 0;
      }
      .update {
        color: #409eff;
        background: #ecf5ff;
        border-color: #b3d8ff;
        border-left-color: none;
        border-right-color: rgba(255, 255, 255, 0.5);
      }
      .update:hover {
        color: #fff;
        background-color: #409eff;
        border-color: #409eff;
      }
      .update:active {
        background: #3a8ee6;
        border-color: #3a8ee6;
        color: #fff;
        outline: 0;
      }
      .delete {
        color: #f56c6c;
        background: #fef0f0;
        border-color: #fbc4c4;
        border-left-color: rgba(255, 255, 255, 0.5);
      }
      .delete:hover {
        color: #fff;
        background-color: #f56c6c;
        border-color: #f56c6c;
      }
      .delete:active {
        background: #dd6161;
        border-color: #dd6161;
        color: #fff;
        outline: 0;
      }
    }
  }
  .sceneForm {
    .el-form {
      .scene-custom-icon {
        border: 1px solid #e8e8e8;
        color: #72767b;
        text-align: center;
        line-height: 24px;
        margin-left: 10px;
      }
      .custom-iconlist {
        display: inline-block;
        width: 40px;
        height: 40px;
        border: #eee 1px solid;
        text-align: center;
        line-height: 40px;
        margin: 0 3px 5px 3px;
        i {
          margin: 0;
          font-size: 20px;
        }
      }
    }
  }
  .scene_actionSet {
    height: 40%;
  }
}
.el-popover {
  .custom-iconlist {
    display: inline-block;
    width: 40px;
    height: 40px;
    color: #409eff;
    background: #ecf5ff;
    border-color: #b3d8ff;
    text-align: center;
    line-height: 40px;
    margin: 0 3px 5px 3px;
    i {
      margin: 0;
      font-size: 20px;
    }
  }
  .custom-iconlist:hover {
    color: #fff;
    background-color: #409eff;
    border-color: #409eff;
  }
  .custom-iconlist:active {
    background: #3a8ee6;
    border-color: #3a8ee6;
    color: #fff;
  }
}
</style>