<template>
  <div class="sckviewer">
    <Calendar
      @MonthChanged="MonthChanged"
      @Changed="DateChanged"
      :ScheduledDate.sync="ScheduleDate"
      :ChooseDate.sync="ChooseDate"
    />
    <p class="title">{{FormatDate(ChooseDate)}}课表信息</p>
    <div class="lists" v-if="schedules">
      <ul>
        <li v-for="(item,index) in schedules" :key="index">
          <div>
            <span>{{item.LessonClsTime}}</span>
            <span>~</span>
            <span>{{item.LessonOutTime}}</span>
          </div>
          <div>
            <p>
              <span>{{item.CSTitle}}</span>
              <span>第{{item.CSLesson}}节</span>
            </p>
            <p>
              <span>教师：{{item.TeacherName}}</span>
            </p>
            <p>
              <span>班级：{{item.ClassName}}</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
    <div v-if="!schedules" class="none">
      <p>{{FormatMonthDay(ChooseDate)}} 无排课</p>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import api from "@/scripts/api";
export default {
  components: {
    Calendar: () => import("@/components/Calendar")
  },
  props: {},
  data() {
    return {
      ScheduleDate: [],
      ChooseDate: new Date(),
      schedules: null
    };
  },
  mounted() {
    this.getCalendarInfo(
      moment(this.ChooseDate).format("YYYY-MM") + "-01",
      moment(this.ChooseDate).format("Y-MM") +
        "-" +
        moment(moment(this.ChooseDate).format("Y-MM"), "Y-MM").daysInMonth()
    );
    this.getTimetableInfo(moment(this.ChooseDate).format("YYYY-MM-DD"));
  },
  methods: {
    FormatDate(day) {
      return moment(day).format("YYYY-MM-DD");
    },
    FormatMonthDay(day) {
      return moment(day).format("MM-DD");
    },
    DateChanged(date) {
      this.getTimetableInfo(this.FormatDate(date));
    },
    MonthChanged(month) {
      this.getCalendarInfo(month[0], month[1]);
    },
    async getCalendarInfo(sDate, eDate) {
      const roomid = this.$parent.$route.params.id;
      const res = await api.Schedule.GetScheduleIntimeByClsID(
        roomid,
        sDate,
        eDate
      );
      this.ScheduleDate = [];
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(element => {
          this.ScheduleDate.push(element.CSDate);
        });
      }
    },
    async getTimetableInfo(date) {
      const roomid = this.$parent.$route.params.id;
      this.schedules = [];
      const res = await api.Schedule.GetScheduleTheDayByClsID(roomid, date);
      if (res.Datas && res.Datas.length > 0) {
        res.Datas.forEach(element => {
          const classInfo = element.ClassInfo
            ? JSON.parse(element.ClassInfo)
            : null;
          let className = "";
          if (classInfo && classInfo.length > 0) {
            classInfo.forEach(ele => {
              className += ele.ClassName + "、";
            });
            className = className.substring(0, className.length - 1);
          }
          const thcInfo = element.ThcInfo ? JSON.parse(element.ThcInfo) : null;
          let tchName = "";
          if (thcInfo && thcInfo.length > 0) {
            thcInfo.forEach(ele => {
              tchName += ele.TeacherName + "、";
            });
            tchName = tchName.substring(0, tchName.length - 1);
          }
          this.schedules.push({
            LessonClsTime: element.LessonClsTime,
            LessonOutTime: element.LessonOutTime,
            CSTitle: element.ScheduleInfo.CSTitle,
            CSLesson: element.ScheduleInfo.CSLesson,
            TeacherName: tchName,
            ClassName: className
          });
        });
      } else {
        this.schedules = null;
      }
    }
  }
};
</script>

<style lang="less">
.sckviewer {
  .none {
    > p {
      font-size: 48px;
      text-align: center;
      line-height: 300px;
      color: #c7cdd3;
    }
  }
}
</style>