<template>
  <div>
    <Table ref="table" :ShowTopBar="false" :ShowTblFoot="false" :ShowSelectColumn="false" :ShowExport="false"
           :Columns="tableColumns" :Datas.sync="dataList" :Loading="dataLoading" :showMaxeight="false">
      <template slot="oprate" slot-scope="scope">
        <el-button type="warning" icon="el-icon-view" size="mini" @click="getPracticeDetail(scope.row)">查看答题</el-button>
      </template>
    </Table>

    <el-dialog class="cp_detail" title="答题明细" :visible.sync="dialogDetailVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <div class="mb" v-for="(item, index) in cpDetail" :key="index">
        <div class="mb">{{item.PSData.PSSerial}}. [{{fetchPSType(item.PSData.PSType)}}] {{item.PSData.PSScore}}分</div>
        <div class="mb">{{item.PSData.PSContent}}</div>
        <div class="mb" v-if="item.PSData.PSType === 1 || item.PSData.PSType === 2">
          <div v-for="(soItem, index) in item.SOData" :key="index" :item="soItem">
            {{soItem.SOSerial}}. {{soItem.SOContent}}
          </div>
        </div>
        <div class="mb">
          答案：
          <span class="primary">{{item.PSData.PSAnswer}}</span>
        </div>
        <div class="mb">
          解析：
          <span class="danger">{{item.PSData.PSanalysis}}</span>
        </div>

        <div class="part_title">我的答题</div>
        <div v-html="item.PAData">
        </div>
      </div>
      <div style="text-align: right;">
        <el-button size="mini" @click="dialogDetailVisible=false">关闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { getSession } from "@/scripts/utils";
import { practiceTypeName } from "@/scripts/dictionary";

export default {
  name: "ClassPractice",
  components: {
    Table: () => import("@/components/Table")
  },
  data () {
    return {
      courseRecordID: getSession("campus_CourseRecordID"),
      courseRecord: getSession("campus_CourseRecord"),

      dataList: [],
      dataLoading: false,
      tableColumns: [
        {
          Code: "CPTitle",
          Header: "名称",
          width: 300
        },
        {
          Code: "SubjectTotal",
          Header: "题数"
        },
        {
          Code: "SubjectScore",
          Header: "总分"
        },
        // { prop: "CPTime", label: "计时(分)" },
        {
          Code: "oprate",
          Header: "操作",
          Slot: true,
          width: 150
        }
      ],

      dialogDetailVisible: false,
      cpDetail: []
    }
  },
  created () {
    this.getClassPracticeStats();
  },
  methods: {
    // 获取随堂练习
    getClassPracticeStats () {
      this.dataLoading = true;
      this.dataList = [];
      api.IClassPractise.dataStatistics(
        this.courseRecordID
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              CIPID: item["CIPID"],
              CPID: item.Key.CPID,
              PLID: item.Key.PLID,
              CPTitle: item.PLTitle,
              SubjectTotal: item.Total,
              SubjectScore: item.Score,
              // CPTime: item.Key.CPTime,
              CreateTime: item.Key.CreateTime,
              State: item.Key.State
            });
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },

    // 获取学生答题
    async getPracticeDetail (row) {
      this.cpDetail = [];

      const studentInfo = store.GetStudentInfo();
      this.dialogDetailVisible = true;
      const pictureURL = this.$root.PictureURL;
      api.IClassPractise.PractiseAnswerList(
        row.CIPID,
        studentInfo ? studentInfo.StudentID : ""
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            let newContent = "未作答";
            if (item.PAData != null) {
              const htmstr = item.PAData.PAContent;
              newContent = htmstr.replace(
                /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                function(match, capture) {
                  const filename = capture.split('/')[1];
                  return '<img src="' + pictureURL + '?filename=' + filename + '&fileType=7" alt="" style="max-width:380px"/>';
                }
              );
            }
            this.cpDetail.push({
              PSData: item.PSData,
              PAData: newContent,
              SOData: item.SOList
            });
          });
        }
      }).catch(() => {
      });
    },

    // 获取题目类型
    fetchPSType (val) {
      return practiceTypeName(val);
    }
  }
}
</script>

<style lang='less'>
.cp_detail {
  .cp_student {
    margin-bottom: 20px;
    font-size: 15px;
  }

  .mb {
    margin-bottom: 15px;

    .primary {
      color: #409eff;
    }

    .danger {
      color: #f56c6c;
    }
  }
}
</style>
