<template>
  <div class="screen_question">

    <el-row :gutter="20" v-loading="dataLoading">
      <el-col :span="6" v-for="(item, index) in dataList" :key="index">
        <div class="question_item" @click="initDialog(item['Type'])">
          <div>【{{item.name}}】</div>
          <div class="card_line">
            <span> 题数：{{item['Total']}}</span>
          </div>
        </div>
      </el-col>
    </el-row>

    <el-dialog title="截屏提问列表" :visible.sync="dialogVisible" :close-on-click-modal="false"
               :modal-append-to-body="false">
      <el-tabs type="border-card" v-model="selectType" @tab-click="selectTypeChange">
        <el-tab-pane v-for="(item, index) in dataList" :key="index" :label="item.name" :name="item['Type'] + ''">
          <keep-alive>
            <el-table :data="typeDataList" max-height="550" v-loading="listLoading" stripe type="expand"
                      :default-sort="{prop: 'SSSerial', order: 'ascending'}">
              <el-table-column type="expand">
                <template slot-scope="props">
                  <screen-detail :cpdata="props.row" :subjectId="props.row['SSID']+''"></screen-detail>
                </template>
              </el-table-column>
              <el-table-column property="SSSerial" label="题号">
              </el-table-column>

              <el-table-column property="SSPatternName" label="模式">
                <template slot-scope="scope">
                  <span>{{getPatternName(scope.row.SSPattern)}}</span>
                </template>
              </el-table-column>

              <el-table-column property="SSScore" label="总分">
              </el-table-column>

              <el-table-column property="SSTime" label="计时(分)">
              </el-table-column>

              <el-table-column property="CreateTime" label="时间" width="200px">
                <template slot-scope="scope">
                  <span>{{formatDate(scope.row.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
                </template>
              </el-table-column>

              <el-table-column property="State" label="状态">
                <template slot-scope="scope">
                  <span>{{getStateName(scope.row.State)}}</span>
                </template>
              </el-table-column>

            </el-table>
          </keep-alive>
        </el-tab-pane>
      </el-tabs>

    </el-dialog>
  </div>
</template>

<script>
// import screenDetail from "./ScreenDetail";
import api from "@/scripts/api";
import { FormatDate, getSession } from "@/scripts/utils";
import {
  practiceTypeName as screenQuestionTypeName,
  screenPatternName,
  screenSubjectStateName
} from "@/scripts/dictionary";

export default {
  name: "ScreenQuestion",
  components: {
    ScreenDetail: () => import("./ScreenDetail")
  },
  data () {
    return {
      // course: JSON.parse(sessionStorage.getItem("course")),
      // courseID: sessionStorage.getItem("courseID"),
      courseRecordID: getSession("campus_CourseRecordID"),
      courseRecord: getSession("campus_CourseRecord"),

      dataList: [],
      dataLoading: false,

      dialogVisible: false,
      selectType: null,
      listLoading: false,
      typeDataList: []
    }
  },
  created () {
    this.getScreenQuestionStats();
  },
  methods: {
    // 获取截屏提问类型统计
    getScreenQuestionStats () {
      this.dataLoading = true;
      this.dataList = [];
      api.IScreenSubject.DataOptionStatistics(
        this.courseRecordID
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push({
              ...item,
              name: screenQuestionTypeName(item["Type"])
            });
          });
        }
        this.dataLoading = false;
      }).catch(() => {
        this.dataLoading = false;
      });
    },
    initDialog (type) {
      if (type) {
        this.selectType = type + "";
      } else if (this.dataList.length > 0) {
        this.selectType = this.dataList[0]["Type"] + "";
      }
      this.selectTypeChange();
      this.dialogVisible = true;
    },
    // 切换题型
    selectTypeChange () {
      this.listLoading = true;
      this.typeDataList = [];
      api.IScreenSubject.DataList(
        this.courseRecordID,
        this.selectType
      ).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.typeDataList.push(item);
          });
        }
        this.listLoading = false;
      }).catch(() => {
        this.listLoading = false;
      });
    },
    getPatternName (val) {
      return screenPatternName(val);
    },
    formatDate (dateValue, format) {
      return FormatDate(dateValue, format);
    },
    getStateName (val) {
      return screenSubjectStateName(val);
    }
  }
}
</script>

<style lang="less" scoped>
.screen_question {
  .question_item {
    width: 100%;
    background-color: #f0f9eb;
    display: flex;
    justify-content: space-between;

    padding: 0 10px;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    color: #67c23a;
    border: 1px solid #e1f3d8;
    border-radius: 4px;
    box-sizing: border-box;
    white-space: nowrap;
  }
}
</style>
