<template>
  <el-cascader
    popper-class="CascaderTBM"
    ref="cascader"
    v-model="Value"
    :placeholder="placeholder"
    :options="options"
    :props="oprops"
    :show-all-levels="false"
    clearable
    collapse-tags
    :size="size"
    @change="ChangeHandle"
    :key="DeviceType"
  >
    <template slot-scope="{ node, data }">
      <span>{{ data.Name }}</span>
    </template>
  </el-cascader>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini",
    },
    value: {},
    placeholder: {
      type: String,
      default: "请选择",
    },
    Values: {
      type: Array,
      default: () => [],
    },
    /**
     * 显示级别：
     * 1: 设备类型
     * 2: 品牌 - 型号
     */
    Level: {
      type: Number,
      default: 1,
    },
    // 设备类型
    DeviceType: {
      type: Number,
      default: -1,
    },
    /**
     * 选择任意一级选项
     */
    checkStrictly: {
      type: Boolean,
      default: false,
    },
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value(newVal, oldVal) {
      this.Value = newVal;
    },
    DeviceType(newVal, oldVal) {
      this.options.forEach((element) => {
        if (this.DeviceType < 0 || element.IDs.indexOf(this.DeviceType) >= 0) {
          this.$set(element, "disabled", false);
        } else {
          this.$set(element, "disabled", true);
        }
      });
    },
  },
  data() {
    return {
      Value: this.value,
      options: this.Level === 1 ? null : [],
      oprops: {
        multiple: this.multiple,
        checkStrictly: this.checkStrictly,
        emitPath: false,
        expandTrigger: "hover",
        value: "ID",
        id: "ID",
        label: "Name",
        children: "options",
      },
    };
  },
  mounted() {
    this.LoadData();
  },
  methods: {
    ChangeHandle(val) {
      this.$emit("update:value", this.Value);
      const checkedNodes = this.$refs.cascader.getCheckedNodes();
      if (checkedNodes && checkedNodes.length > 0) {
        this.$emit(
          "update:Values",
          this.$refs.cascader.getCheckedNodes()[0].path
        );
        const code = [];
        const name = [];
        checkedNodes[0].pathLabels.forEach((item) => {
          if (item.match(/\((.+?)\)/)) {
            code.push(item.match(/\((.+?)\)/)[1]);
          }
          name.push(item.trim().split(" ")[0]);
        });
        this.$emit("update:code", code);
        this.$emit("update:name", name);
      } else {
        this.$emit("update:Values", []);
        this.$emit("update:code", []);
        this.$emit("update:name", []);
      }

      const LZString = require("lz-string");
      const jsonStr = JSON.stringify(val);
      let result = "";
      if (jsonStr) {
        result = LZString.compressToBase64(
          jsonStr.slice(1, jsonStr.length - 1)
        );
      }

      this.$emit("Filter", result);
      this.$emit("ChangeModel", val);
    },
    /**
     * 加载数据
     */
    async LoadData() {
      this.options = [
        {
          Level: 1,
          ID: 1,
          IDs: [1],
          Name: "中控、终端",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 2,
          IDs: [2],
          Name: "PC",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 3,
          IDs: [3],
          Name: "IP电话",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 4,
          IDs: [4],
          Name: "摄像头",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 5,
          IDs: [5],
          Name: "课件编码器",
          options: this.Level === 1 ? null : [],
        },
        // { Level: 1,ID: 6, ID: [6], Name: "服务器",  options: this.Level === 1 ? null : [] },
        {
          Level: 1,
          ID: 7,
          IDs: [7],
          Name: "IC卡读卡器",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 9,
          IDs: [9],
          Name: "录播主机",
          options: this.Level === 1 ? null : [],
        },
        // {
        //   Level: 1,
        //   ID: 10,
        //   IDs: [10],
        //   Name: "无线麦",
        //   options: this.Level === 1 ? null : []
        // },
        {
          Level: 1,
          ID: 11,
          IDs: [11],
          Name: "投影仪",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 12,
          IDs: [12],
          Name: "门禁主机",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 13,
          IDs: [13],
          Name: "电源箱",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 14,
          IDs: [14],
          Name: "音箱",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 8,
          IDs: [8],
          Name: "485物联设备",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 15,
          IDs: [15],
          Name: "LED屏",
          options: this.Level === 1 ? null : [],
        },
        {
          Level: 1,
          ID: 16,
          IDs: [16],
          Name: "互动网关",
          options: this.Level === 1 ? null : [],
        },
      ];

      if (this.Level === 1) {
        this.options.push(
          // {
          //   Level: 1,
          //   ID: 8,
          //   IDs: [8],
          //   Name: "物联网",
          //   options: this.Level === 1 ? null : []
          // },
          // {
          //   Level: 1,
          //   ID: 20,
          //   IDs: [20],
          //   Name: "遥控宝",
          //   options: this.Level === 1 ? null : []
          // },
          // {
          //   Level: 1,
          //   ID: 21,
          //   IDs: [21],
          //   Name: "遥控宝-空调",
          //   options: this.Level === 1 ? null : []
          // },
          // {
          //   Level: 1,
          //   ID: 22,
          //   IDs: [22],
          //   Name: "遥控宝-风扇",
          //   options: this.Level === 1 ? null : []
          // },
          // {
          //   Level: 1,
          //   ID: 23,
          //   IDs: [23],
          //   Name: "遥控宝-窗帘",
          //   options: this.Level === 1 ? null : []
          // },
          // {
          //   Level: 1,
          //   ID: 24,
          //   IDs: [24],
          //   Name: "遥控宝-开关面板",
          //   options: this.Level === 1 ? null : []
          // },
          {
            Level: 1,
            ID: 30,
            IDs: [30],
            Name: "情景面板",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 40,
            IDs: [40],
            Name: "睿和网关",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 41,
            IDs: [41],
            Name: "睿和单开面板",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 42,
            IDs: [42],
            Name: "睿和双开面板",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 43,
            IDs: [43],
            Name: "睿和三开面板",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 44,
            IDs: [44],
            Name: "睿和调光器",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 45,
            IDs: [45],
            Name: "睿和红外转发器",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 46,
            IDs: [46],
            Name: "睿和调速器",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 47,
            IDs: [47],
            Name: "睿和调速器",
            options: this.Level === 1 ? null : [],
          },
          {
            Level: 1,
            ID: 48,
            IDs: [48],
            Name: "原版红外转发器",
            options: this.Level === 1 ? null : [],
          }
        );
      } else {
        this.options.push({
          Level: 1,
          ID: 41,
          IDs: [20, 21, 22, 23, 24, 30, 40, 41, 42, 43, 44, 45, 46, 47, 48],
          Name: "物联网",

          options: this.Level === 1 ? null : [],
        });
      }
      this.options.push({
        Level: 1,
        ID: 9999,
        IDs: [9999],
        Name: "其它",

        options: this.Level === 1 ? null : [],
      });

      this.options.forEach((element) => {
        if (this.DeviceType < 0 || element.IDs.indexOf(this.DeviceType) >= 0) {
          this.$set(element, "disabled", false);
        } else {
          this.$set(element, "disabled", true);
        }
      });

      if (this.Level > 1) {
        const resB = await api.Brand.GetAllDatas(); // 加载所有品牌
        const resM = await api.Model.GetAllDatas(); // 加载所有型号

        if (resB && resM && resM.Datas) {
          resM.Datas.forEach((element) => {
            const findT = this.options.find(
              (t) => t.IDs.indexOf(element.DeviceType) >= 0
            );
            if (findT) {
              let findB = findT.options.find((b) => b.ID === element.BrandID);
              if (findB) {
              } else {
                const findBB = resB.Datas.find(
                  (b) => b.BrandID === element.BrandID
                );

                if (findBB) {
                  findB = {
                    Level: 2,
                    ID: findBB.BrandID,
                    Name: `${findBB.BrandName} (${findBB.BrandCode})`,

                    options: this.Level === 1 ? null : [],
                  };
                  findT.options.push(findB);
                }
              }

              if (findB) {
                findB.options.push({
                  Level: 3,
                  ID: element.ModelID,
                  Name: `${element.ModelName} (${element.ModelCode})`,
                  options: null,
                });
              }
            }
          });
        }
      }
    },
  },
};
</script>

<style lang="less">
.CascaderTBM {
  .el-cascader-node.is-disabled {
    display: none;
  }
}
</style>
