<template>
  <el-select
    filterable
    :multiple="multiple"
    v-model="Value"
    :placeholder="placeholder"
    @change="ChangeHandle"
    clearable
    collapse-tags
    :size="size"
    reserve-keyword
  >
    <el-option
      v-for="item in Datas"
      :key="item.Key.TermID"
      :label="`${item.Key.TermName} ${item.XQ} `"
      :value="item.Key.TermID"
    ></el-option>
  </el-select>
</template>

<script>
import api from "@/scripts/api";

export default {
  props: {
    size: {
      type: String,
      default: "mini"
    },
    /**
     * 多选
     */
    multiple: {
      type: Boolean,
      default: false
    },
    value: {},
    text: {
      type: String,
      default: ""
    },
    TooltipPlacement: {
      type: String,
      default: "right"
    },
    placeholder: {
      type: String,
      default: "请输入关键词"
    }
  },
  data() {
    return { Value: this.value, Datas: [] };
  },
  mounted() {
    this.LoadData();
  },
  watch: {
    value(val) {
      this.Value = val;
      this.LoadData();
    }
  },
  methods: {
    ChangeHandle() {
      this.$emit("update:value", this.Value);
      if (this.Value && this.multiple) {
        let val = "";
        this.Value.forEach((f, i) => {
          val += f;
          if (i < this.Value.length - 1) {
            val += ",";
          }
        });

        this.$emit("Filter", val);
      }
    },
    async LoadData() {
      const res = await api.Term.GetDatas();
      if (res) {
        this.Datas = res.Datas;
      }
    }
  }
};
</script>

<style lang="less"></style>
